*{
  margin: 0px;
  padding: 0px;
}

.head{
  background-color: darkblue;
  height: 50px;
  color : white;
  padding-left: 50px;
  position: relative;
  cursor: pointer;
}
.modal_screen{
  position: absolute;

  width: 250px;
  height: 250px;
  padding : 30px;
  border: 1px solid black;
  
  background-color: #fff;
  color : #333;
  text-align: center;
  top : 100px;
  left: 10%;
  z-index: 5;

  transition: 0.5;
  overflow: hidden;
}
  .modal_screen.open{
    width: 250px;
    height: 250px;
    padding : 30px;
    border: 1px solid black;
  }

.contents{
  border-bottom: 1px solid darkblue;
  padding : 20px 50px;

  position : relative;
}

.thumbUpButton{
  position: absolute;
  right: 50%;
  top:50%;
  border : 1px solid salmon;
  border-radius: 15px;
  cursor: pointer;
  padding : 10px;
  transform: translate(-50% ,-50%);
}
.modifyTextButton, .sortTextButton, .modalButton{
  margin-top : 30px;
  margin-left: 50px;
}

.modifyTextButton span, .sortTextButton span, .modalButton{
  display: block;
  padding : 10px;

}